import React, { Component } from 'react'
import cx from 'classnames'
import PropTypes from 'prop-types'
import ModalPlayer from 'Shared/components/ModalPlayer'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from 'Shared/components/FormElements/Label'
import Button, { BigJustIconButton } from 'Shared/components/Button'
import DeleteModal from 'Shared/components/DeleteModal'

class UploadedVideoDetails extends Component {
  static propTypes = {
    videoCvUrl: PropTypes.string,
    width: PropTypes.number,
    height: PropTypes.number,
    onDelete: PropTypes.func.isRequired,
    size: PropTypes.oneOf(['big', 'small']).isRequired
  }

  static defaultProps = {
    videoWidth: 272,
    videoHeight: 152,
    size: 'small'
  }

  constructor (props) {
    super(props)
    this.state = {
      showDeleteModal: false,
      videoLoaded: false,
      videoError: null
    }
  }

  triggerVideoError = err => this.setState({ videoError: err })

  triggerVideoLoadedSuccess = () => this.setState({ videoLoaded: true })

  _openDeleteModal = () => this.setState({ showDeleteModal: true })
  _hideDeleteModal = () => this.setState({ showDeleteModal: false })

  render () {
    const { t, videoCvUrl, onDelete, size } = this.props
    const containerClass = cx({
      'video-uploader__container--video': size === 'small',
      'video-uploader__container--video-big': size === 'big'
    })

    const playerClass = cx({
      'video-uploader__player': true,
      'video-uploader__player--loading': !this.state.videoLoaded
    })

    const ButtonComponent = size === 'small' ? Button : BigJustIconButton
    const controlsClass = cx({
      'video-uploader__container--controls': size === 'small',
      'video-uploader__container--controls-big': size === 'big'
    })

    const noticeClass = cx({
      'video-uploader__notice': size === 'small',
      'video-uploader__notice--big': size === 'big'
    })

    return (
      <div className='video-uploader__uploaded-video-details'>
        <div className={containerClass}>
          <div className='video-uploader__container__video-wrapper'>
            { size === 'small' && <Label>{t('common.uploadedVideo')}</Label> }
            <div>
              {this.state.videoError
                ? (
                  <div className={noticeClass}>
                    <i className='video-uploader__notice-icon icon-im-step-done' />
                    <h4 className='video-uploader__notice-heading'>{t('videoUploadNotice.heading')}</h4>
                    <div className='video-uploader__notice-description'>{t('videoUploadNotice.description')}</div>
                  </div>
                )
                : (
                  <ModalPlayer
                    className={playerClass}
                    videoUrl={videoCvUrl}
                    onReady={this.triggerVideoLoadedSuccess}
                    onError={this.triggerVideoError}
                  />
                )
              }
            </div>
          </div>
          <div className={controlsClass}>
            <ButtonComponent onClick={this._openDeleteModal} justIcon><i className='ion-md-trash' /></ButtonComponent>
          </div>
        </div>
        <DeleteModal
          title={this.props.t('remove.video.title')}
          content={this.props.t('remove.video.description')}
          handleDelete={onDelete}
          isModalVisible={this.state.showDeleteModal}
          hideDeleteModal={this._hideDeleteModal}
        />
      </div>
    )
  }
}

export default withTranslations(UploadedVideoDetails)



