import React from 'react'
import PropTypes from 'prop-types'
import './ProgressBar.scss'

const ProgressBar = ({ min, max, label }) => {
  const getWidth = () => Math.round((parseInt(min) / parseInt(max)) * 100)
  const progressStyles = {
    width: `${getWidth()}%`
  }

  return (
    <div className='c-progress-bar'>
      <div className='c-progress-bar__total'>
        <div style={progressStyles} className='c-progress-bar__progress' />
      </div>
      {label && <div className='c-progress-bar__label'><span>{label}</span> {getWidth()}%</div>}
    </div>
  )
}

ProgressBar.propTypes = {
  min: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string
  ]).isRequired,
  max: PropTypes.number.isRequired,
  label: PropTypes.string
}

export default ProgressBar



