import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Dropzone from 'react-dropzone'
import { head, join } from 'ramda'
import { Interpolate } from 'react-i18next'
import NotificationService from 'Shared/services/NotificationService'
import { avatarFileName } from 'Shared/helpers/files'
import { UPLOAD_VIDEO_TYPES, UPLOAD_VIDEO_MAX_SIZE } from 'Shared/constants/Files'
import withTranslations from 'Shared/hoc/withTranslations'
import ProgressBar from 'Shared/components/ProgressBar/ProgressBar'

import UploadedVideoDetails from './UploadedVideoDetails'
import './VideoUploader.scss'

const _uploadFiles = files => console.log('upload files', files)

class VideoUploader extends Component {
  dropzoneRef = {}

  state = {
    files: [],
    fileSize: 0
  }

  static propTypes = {
    acceptedMimeTypes: PropTypes.array,
    maxFileSize: PropTypes.number,
    uploadVideo: PropTypes.func,
    deleteVideo: PropTypes.func.isRequired,
    t: PropTypes.func,
    requirement: PropTypes.bool,
    handleException: PropTypes.func,
    size: PropTypes.oneOf(['big', 'small']).isRequired
  }

  static defaultProps = {
    acceptedMimeTypes: UPLOAD_VIDEO_TYPES,
    maxFileSize: UPLOAD_VIDEO_MAX_SIZE,
    uploadVideo: _uploadFiles,
    requirement: true,
    size: 'small'
  }

  onDrop = files => files.length && this.setState({fileSize: head(files).size})

  uploadVideo = (file, filename) => {
    if (!this.props.requirement) {
      return this.props.handleException()
    }

    return this.props.uploadVideo(file, filename)
  }

  handleReject = (files) => {
    const { t, maxFileSize } = this.props
    return files[0].size > maxFileSize
      ? NotificationService.info(t('alerts.fileToLarge', {limit: maxFileSize / 1024 / 1024}))
      : NotificationService.info(t('alerts.fileRejected'))
  }

  renderVideoComponent = () => {
    const { videoCvUrl, acceptedMimeTypes, maxFileSize, size } = this.props
    return videoCvUrl
      ? <div>
        <UploadedVideoDetails
          videoCvUrl={videoCvUrl}
          filename={avatarFileName(videoCvUrl)}
          onDelete={this.props.deleteVideo}
          size={size}
          videoWidth={size === 'small' ? 272 : 681}
          videoHeight={size === 'small' ? 272 : 345}
        />
      </div>
      : <div className='video-uploader'>
        <Dropzone
          {...this.props.input}
          accept={join(',', acceptedMimeTypes)}
          onDropAccepted={this.uploadVideo}
          onDropRejected={this.handleReject}
          maxSize={maxFileSize}
          className='video-uploader__dropzone'
          activeClassName='video-uploader__dropzone--active'
          disableClick
          ref={node => {
            this.dropzoneRef = node
          }}
          onDrop={this.onDrop}
          multiple={false}
        >
          <div className='video-uploader__container'>
            <div className='video-uploader__drop-area'>
              <i className='icon-im-upload' />
              <Interpolate
                parent='div'
                i18nKey='common.uploadVideoInfo'
                className='video-uploader__description'
                uploadButton={this.uploadButton()}
              />
            </div>
          </div>
        </Dropzone>
      </div>
  }

  uploadButton = () => (
    <span className='video-uploader__button--upload' onClick={() => this.dropzoneRef.open()}>
      {this.props.t('common.choose')}
    </span>
  )

  render () {
    const { uploading, t } = this.props
    const { fileSize } = this.state
    return (
      <div>
        {uploading
          ? <ProgressBar min={uploading} max={fileSize} label={t('common.uploadProgress')} />
          : this.renderVideoComponent()}
      </div>
    )
  }
}

export default withTranslations(VideoUploader)



